import { Component } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { GeneralDeviceRegistrationComponent } from './general-device-registration.component';

@Component({
  selector: 'c8y-general-device-registration-button',
  templateUrl: 'general-device-registration-button.component.html'
})

export class GeneralDeviceRegistrationButtonComponent {
  constructor(private modalService: BsModalService) {}

  open() {
    this.modalService.show(GeneralDeviceRegistrationComponent, {
      class: 'modal-sm',
      ignoreBackdropClick: true
    });
  }
}
