import { Component, OnInit } from '@angular/core';
import { BulkExtensibleDeviceRegistrationService } from './extensible-bulk-device-registration.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ExtensibleBulkDeviceRegistrationModalComponent } from './extensible-bulk-device-registration-modal.component';
import { ExtensibleDeviceRegistrationProvider } from '../base-device-registration.model';

@Component({
  selector: 'extensible-device-registration-component',
  templateUrl: 'extensible-bulk-device-registration-button.component.html'
})

export class ExtensibleBulkDeviceRegistrationButtonComponent implements OnInit {
  bulkProvidersMetaData: Promise<ExtensibleDeviceRegistrationProvider[]>;

  constructor(
    private bulkExtensibleDeviceRegistrationService: BulkExtensibleDeviceRegistrationService,
    private modalService: BsModalService
  ) {}

  ngOnInit() {
    this.bulkProvidersMetaData = this.bulkExtensibleDeviceRegistrationService.getDeviceRegistrationProviders();
  }

  click(contextPath) {
    this.modalService.show(ExtensibleBulkDeviceRegistrationModalComponent, {
      initialState: { contextPath } as Partial<ExtensibleBulkDeviceRegistrationModalComponent>,
      class: 'modal-sm',
      ignoreBackdropClick: true
    });
  }
}
