import { Injectable } from '@angular/core';
import { ApplicationService, FetchClient, IResult } from '@c8y/client';
import { AppStateService, gettext } from '@c8y/ngx-components';
import { BaseExtensibleDeviceRegistrationService } from '../base-extensible-device-registration.service';
import { ExtensibleDeviceRegistrationSchema } from './extensible-device-registration.model';
import { ExtensibleDeviceRegistrationProvider } from '../base-device-registration.model';

@Injectable()
export class ExtensibleDeviceRegistrationService extends BaseExtensibleDeviceRegistrationService {
  constructor(
    protected applicationService: ApplicationService,
    protected stateService: AppStateService,
    private client: FetchClient
  ) {
    super(applicationService, stateService);
  }

  async getSchema(contextPath: string): Promise<ExtensibleDeviceRegistrationSchema> {
    const response = await this.client.fetch(this.getMetadataUrl(contextPath), { params: { lang: this.stateService.state.lang } });
    const data = await response.json();

    if (!data || !data.c8y_DeviceRegistration) {
      throw new Error(gettext('The response of the microservice is incomplete. The device registration information is missing.'));
    }

    return data.c8y_DeviceRegistration;
  }

  getDeviceRegistrationProviders(): Promise<ExtensibleDeviceRegistrationProvider[]> {
    return super.getDeviceRegistrationProviders(false);
  }

  async registerDevice(contextPath: string, model: any): Promise<IResult<any>> {
    const method = 'POST';
    const body = JSON.stringify(model);
    const headers = { 'content-type': 'application/json', accept: 'application/json' };
    const res = await this.client.fetch(this.getServiceBaseUrl(contextPath), { method, body, headers });
    const data = await res.json();
    return { res, data };
  }

}
