import { Component, OnDestroy, OnInit } from '@angular/core';
import {
  DeviceRegistrationSecurityMode,
  DeviceRegistrationStatus,
  IDeviceRegistration,
  IDeviceRegistrationLimit,
  Paging
} from '@c8y/client';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { filter, map, switchMap, takeUntil } from 'rxjs/operators';
import {
  DeviceBootstrapRealtimeService,
  IRealtimeDeviceBootstrap,
  TenantUiService,
  ModalService,
  Status,
  gettext,
  OptionsService
} from '@c8y/ngx-components';
import { RegisterDeviceService } from './register-device.service';
import { sortBy } from 'lodash-es';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'c8y-device-registration-view',
  templateUrl: 'device-registration-view.component.html'
})
export class DeviceRegistrationViewComponent implements OnInit, OnDestroy {
  deviceRequests$: Observable<{
    data: IDeviceRegistration[];
    paging?: Paging<IDeviceRegistration>;
  }>;
  limit$: Observable<IDeviceRegistrationLimit>;
  limitReachedInfo$: Observable<string>;
  requireSecurityToken: boolean = false;
  isManagementTenant: boolean = false;
  isLoading: boolean = false;
  gridOrList: 'interact-list' | 'interact-grid' = 'interact-grid';
  status = DeviceRegistrationStatus;

  readonly statusProps = {
    [DeviceRegistrationStatus.WAITING_FOR_CONNECTION]: {
      label: gettext('Waiting for connection'),
      icon: 'unlink',
      cls: 'text-danger'
    },
    [DeviceRegistrationStatus.PENDING_ACCEPTANCE]: {
      label: gettext('Pending acceptance'),
      icon: 'circle',
      cls: 'text-info'
    },
    [DeviceRegistrationStatus.ACCEPTED]: {
      label: gettext('Accepted'),
      icon: 'check-circle',
      cls: 'text-success'
    },
    [DeviceRegistrationStatus.BLOCKED]: {
      label: gettext('Blocked'),
      icon: 'remove',
      cls: 'text-danger'
    }
  };

  private unsubscribe$: Subject<any> = new Subject();
  private readonly _securityTokenPolicy: BehaviorSubject<DeviceRegistrationSecurityMode> =
    new BehaviorSubject(DeviceRegistrationSecurityMode.OPTIONAL);

  constructor(
    private registerDeviceService: RegisterDeviceService,
    private bootstrapRealtimeService: DeviceBootstrapRealtimeService,
    private tenantUiService: TenantUiService,
    private modalService: ModalService,
    private translateService: TranslateService,
    private optionsService: OptionsService
  ) {}

  ngOnInit() {
    this.loadAll();
    this.setIsManagementTenant();
    this.setRequireSecurityToken();

    this.deviceRequests$ = this.registerDeviceService.deviceRegistrationRequests$.pipe(
      map(req => ({
        data: sortBy(req.data, [
          ({ status }) => (status === DeviceRegistrationStatus.PENDING_ACCEPTANCE ? 0 : 1),
          '-creationTime'
        ]),
        paging: req.paging
      }))
    );
    this.limit$ = this.registerDeviceService.limit$;
    this.limitReachedInfo$ = this.limit$.pipe(
      filter(deviceRegistrationLimit => deviceRegistrationLimit.isReached),
      switchMap(({ limit }) =>
        this.translateService.stream(
          gettext(
            'You reached the limit of {{ maxDevices }} devices. No more devices can be registered.'
          ),
          { maxDevices: limit }
        )
      )
    );
    this.registerDeviceService.loading$
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(value => (this.isLoading = value));

    this.bootstrapRealtimeService
      .onUpdate$()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((bootstrap: IRealtimeDeviceBootstrap) => {
        this.registerDeviceService.onDeviceBootstrap(bootstrap);
      });
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  updateList(data) {
    this.registerDeviceService.internalListUpdate(data);
  }

  async delete(id: string) {
    const confirmed = await this.modalService.confirm(
      gettext('Cancel device registration'),
      this.translateService.instant(
        gettext(
          'You are about to cancel device registration for ID "{{id}}". Do you want to proceed?'
        ),
        { id }
      ),
      Status.DANGER,
      {
        ok: gettext('Cancel registration'),
        cancel: gettext('Close')
      }
    );

    if (confirmed) {
      this.registerDeviceService.remove(id);
    }
  }

  accept(request: IDeviceRegistration) {
    this.registerDeviceService.accept(request);
  }

  acceptAll() {
    this.registerDeviceService.acceptAll();
  }

  canAcceptAll() {
    const pendingRequests = this.registerDeviceService.getRequestByStatus(
      DeviceRegistrationStatus.PENDING_ACCEPTANCE
    );
    return !(pendingRequests.length > 0 && !this.requireSecurityToken);
  }

  loadAll() {
    this.registerDeviceService.list();
  }

  displayMode(listClass: 'interact-list' | 'interact-grid') {
    this.gridOrList = listClass;
  }

  async setRequireSecurityToken() {
    const mode: DeviceRegistrationSecurityMode = await this.optionsService.getTenantOption(
      'device-registration',
      'security-token.policy',
      DeviceRegistrationSecurityMode.OPTIONAL
    );
    this._securityTokenPolicy.next(mode);
    this.requireSecurityToken = mode === DeviceRegistrationSecurityMode.REQUIRED;
  }

  async setIsManagementTenant() {
    this.isManagementTenant = await this.tenantUiService.isManagementTenant();
  }

  shouldShowSecurityTokenInput(data: IDeviceRegistration) {
    return (
      data &&
      data.status === DeviceRegistrationStatus.PENDING_ACCEPTANCE &&
      this.showTokenInputBasedOnSecurityMode()
    );
  }

  showTokenInputBasedOnSecurityMode() {
    return this._securityTokenPolicy.getValue() !== DeviceRegistrationSecurityMode.IGNORED;
  }
}
