import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory, Permissions } from '@c8y/ngx-components';

@Injectable()
export class RegisterDeviceNavigationFactory implements NavigatorNodeFactory {
  constructor(private permissions: Permissions) {}
  get() {
    const userHasPermission = this.permissions.hasAnyRole([
      'ROLE_DEVICE_CONTROL_ADMIN',
      'ROLE_DEVICE_CONTROL_READ'
    ]);
    const nav: NavigatorNode[] = [];
    if (userHasPermission) {
      nav.push(new NavigatorNode({
        label: gettext('Registration'),
        icon: 'c8y-device-connect',
        path: '/deviceregistration',
        parent: gettext('Devices'),
        preventDuplicates: true,
        priority: 3000
      }));
    }
    return nav;
  }
}
