import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RegisterDeviceService } from '../register-device.service';
import { RegisterDeviceExtensionService } from './register-device-extension.service';

@Component({
  selector: 'c8y-register-device-dropdown',
  templateUrl: './register-device-dropdown.component.html'
})
export class RegisterDeviceDropdownComponent {
  single$ = this.registerDeviceExtensionService.items$.pipe(
    map(items => items.filter(item => item.category === 'single').sort((a, b) =>  b.priority - a.priority))
  );

  bulk$ = this.registerDeviceExtensionService.items$.pipe(
    map(items => items.filter(item => item.category === 'bulk').sort((a, b) =>  b.priority - a.priority))
  );

  limit$: Observable<boolean> = this.registerDeviceService.limit$.pipe(map(limit => limit.isReached));

  constructor(
    private registerDeviceExtensionService: RegisterDeviceExtensionService,
    private registerDeviceService: RegisterDeviceService) {}
}
