import { Component, OnInit, ViewChild } from '@angular/core';
import { C8yJSONSchema, C8yStepper, gettext } from '@c8y/ngx-components';
import { FormGroup } from '@angular/forms';
import {
  BulkDescription,
  BulkFailedResult,
  BulkFileExample,
  BulkResult
} from './extensible-bulk-device-registration.model';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { BulkExtensibleDeviceRegistrationService } from './extensible-bulk-device-registration.service';
import { RegisterDeviceService } from '../../register-device.service';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { IResult } from '@c8y/client';

const bulkSchema: object = {
  $schema: 'https://json-schema.org/draft/2019-09/schema',
  type: 'object',
  properties: {
    csvBulkFile: {
      type: 'string',
      title: gettext('CSV file upload'),
      description: gettext(
        'You can use file upload component to let users send files. This input accepts only a single CSV file.'
      ),
      contentEncoding: 'base64',
      contentMediaType: 'csv'
    }
  },
  required: ['csvBulkFile'],
  additionalProperties: false
};

@Component({
  selector: 'extensible-device-registration-modal-component',
  templateUrl: 'extensible-bulk-device-registration-modal.component.html'
})
export class ExtensibleBulkDeviceRegistrationModalComponent implements OnInit {
  @ViewChild(C8yStepper, { static: true }) stepper: C8yStepper;
  title: string;
  examples: BulkFileExample[];
  message: string;
  success: boolean;
  loadingError: boolean = false;
  pending: boolean = false;
  contextPath: string;
  result: BulkResult | BulkFailedResult;
  form = new FormGroup({});
  model = {};
  template: FormlyFieldConfig[];

  constructor(
    private jsonschema: C8yJSONSchema,
    private bulkExtensibleDeviceRegistrationService: BulkExtensibleDeviceRegistrationService,
    private registerDeviceService: RegisterDeviceService,
    private bsModalRef: BsModalRef
  ) {}

  async ngOnInit() {
    const { title, exampleFileUrls }: BulkDescription =
      await this.bulkExtensibleDeviceRegistrationService
        .getBulkDescription(this.contextPath)
        .catch(error => {
          this.message = error.message;
          this.loadingError = true;

          return {
            title: gettext('Error'),
            exampleFileUrls: []
          };
        });

    this.title = title;
    this.template = [this.jsonschema.toFieldConfig(bulkSchema)];
    this.examples = exampleFileUrls;
  }

  async upload() {
    this.pending = true;
    const { res, data }: IResult<BulkResult> = await this.bulkExtensibleDeviceRegistrationService
      .upload(this.getFile(this.model), this.contextPath)
      .catch(err => {
        return {
          res: undefined,
          data: undefined
        };
      })
      .finally(() => {
        this.pending = false;
        this.stepper.next();
      });

    this.result = data;
    this.success = res?.status < 400;

    if (this.success) {
      this.message = gettext('Device registration created.');
      this.model = {};
    }

    if (res) {
      if (res.status >= 400 && res.status < 500) {
        this.message = gettext('Device registration failed.');
      } else if (res.status >= 500) {
        this.message = gettext('Error occurred while processing the uploaded file.');
      }
    } else {
      this.message = gettext('No internet connection. Unable to reach the microservice.');
    }
  }

  getFile(model) {
    const csvBulkFile = (model as any)?.csvBulkFile;
    return csvBulkFile ? csvBulkFile[0]?.file : undefined;
  }

  complete() {
    this.registerDeviceService.list();
    this.bsModalRef.hide();
  }

  cancel() {
    this.bsModalRef.hide();
  }
}
