import { Component, OnInit } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ExtensibleDeviceRegistrationService } from './extensible-device-registration.service';
import { ExtensibleDeviceRegistrationModalComponent } from './extensible-device-registration-modal.component';
import { ExtensibleDeviceRegistrationProvider } from '../base-device-registration.model';

@Component({
  selector: 'extensible-device-registration-provider',
  templateUrl: 'extensible-device-registration-button.component.html'
})
export class ExtensibleDeviceRegistrationButtonComponent implements OnInit {
  providersMetaData: Promise<ExtensibleDeviceRegistrationProvider[]>;

  constructor(private deviceRegistrationService: ExtensibleDeviceRegistrationService,
              private modalService: BsModalService) {
  }

  ngOnInit() {
    this.providersMetaData = this.deviceRegistrationService.getDeviceRegistrationProviders();
  }

  click(providerMetaData: ExtensibleDeviceRegistrationProvider) {
    const { name, contextPath } = providerMetaData;
    this.modalService.show(ExtensibleDeviceRegistrationModalComponent, {
      initialState: { name, contextPath } as Partial<ExtensibleDeviceRegistrationModalComponent>,
      class: 'modal-sm',
      ignoreBackdropClick: true
    });
  }
}
