import { Component, ViewChild } from '@angular/core';
import { C8yJSONSchema, C8yStepper, gettext } from '@c8y/ngx-components';
import { FormGroup } from '@angular/forms';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { saveAs } from 'file-saver';
import { DeviceRegistrationBulkService, IDeviceRegistrationBulkResult } from '@c8y/client';
import { Router } from '@angular/router';
import { RegisterDeviceService } from '../register-device.service';
import { BulkFailedResult } from '../extensible/bulk/extensible-bulk-device-registration.model';
import { BsModalRef } from 'ngx-bootstrap/modal';

const bulkSchema: object = {
  $schema: 'https://json-schema.org/draft/2019-09/schema',
  type: 'object',
  properties: {
    csvBulkFile: {
      type: 'string',
      title: gettext('CSV file upload'),
      description: gettext(
        'You can use file upload component to let users send files. This input accepts only a single CSV file.'
      ),
      contentEncoding: 'base64',
      contentMediaType: 'csv'
    }
  },
  required: ['csvBulkFile'],
  additionalProperties: false
};

const simpleCsvHeaders: string[] = ['ID', 'PATH'];
const fullCsvHeaders: string[] = [
  'ID',
  'CREDENTIALS',
  'TYPE',
  'NAME',
  'ICCID',
  'IDTYPE',
  'PATH',
  'SHELL',
  'AUTH_TYPE'
];

@Component({
  selector: 'bulk-device-registration',
  templateUrl: 'bulk-device-registration-modal.component.html'
})
export class BulkDeviceRegistrationModalComponent {
  @ViewChild(C8yStepper, { static: true }) stepper: C8yStepper;
  message: string;
  success: boolean;
  pending: boolean;
  result: IDeviceRegistrationBulkResult;
  failedResult: BulkFailedResult;
  form = new FormGroup({});
  model = {};
  template: FormlyFieldConfig[];

  constructor(
    private router: Router,
    private jsonschema: C8yJSONSchema,
    private deviceRegistrationService: DeviceRegistrationBulkService,
    private registerDeviceService: RegisterDeviceService,
    private bsModalRef: BsModalRef
  ) {}

  ngOnInit() {
    this.template = [this.jsonschema.toFieldConfig(bulkSchema)];
  }

  upload() {
    this.pending = true;
    const file = this.getFile(this.model);
    this.deviceRegistrationService
      .create(file)
      .then(({ res, data }) => {
        if (res.status < 400) {
          this.result = data;
          this.success = data.numberOfFailed === 0 && data.numberOfSuccessful === data.numberOfAll;
          this.message = this.success
            ? gettext('Device registration created.')
            : (this.message = gettext('Device registration failed.'));
        } else {
          this.failedResult = data as unknown as BulkFailedResult;
          this.message = gettext('Device registration failed.');
        }
        this.model = {};
        this.pending = false;
        this.stepper.next();
      })
      .catch(reason => {
        this.message = gettext('Error occurred while processing the uploaded file.');
        this.pending = false;
        this.stepper.next();
      });
  }

  downloadSimple() {
    return this.download(simpleCsvHeaders, gettext('Simple bulk registration - template.csv'));
  }

  downloadFull() {
    return this.download(fullCsvHeaders, gettext('Full bulk registration - template.csv'));
  }

  download(headers: string[], fileName: string) {
    const headerRaw = headers.map(header => `"${header}"`).join(';');
    const binaryFile = new Blob([headerRaw], { type: 'text/csv' });
    saveAs(binaryFile, fileName);
  }

  complete() {
    this.registerDeviceService.list();
    this.bsModalRef.hide();
  }

  cancel() {
    this.bsModalRef.hide();
  }

  private getFile(model): File {
    const csvBulkFile = (model as any)?.csvBulkFile;
    return csvBulkFile ? csvBulkFile[0]?.file : undefined;
  }
}
