import { Component, OnInit } from '@angular/core';
import { C8yJSONSchema, gettext } from '@c8y/ngx-components';
import { ExtensibleDeviceRegistrationService } from './extensible-device-registration.service';
import { ExtensibleDeviceRegistrationSchema } from './extensible-device-registration.model';
import { JSONSchema7 } from 'json-schema';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'extensible-device-registration-modal',
  templateUrl: 'extensible-device-registration-modal.component.html'
})

export class ExtensibleDeviceRegistrationModalComponent implements OnInit {

  title: string;
  loadingErrorMessage: string;
  pages: FormlyFieldConfig[][];
  labels: string[] = [];
  contextPath: string;

  constructor(
    public bsModalRef: BsModalRef,
    private jsonschema: C8yJSONSchema,
    private registrationService: ExtensibleDeviceRegistrationService
  ) {}

  async ngOnInit() {
    const { title, pages }: ExtensibleDeviceRegistrationSchema = await this.registrationService.getSchema(this.contextPath)
    .catch(error => {
      this.loadingErrorMessage = error?.message || gettext('Cannot fetch a form definition from the microservice.');
      return {
        title: gettext('Error')
      };
    });
    this.title = title;
    this.pages = pages?.map((step: JSONSchema7) => {
      this.labels.push(step.title);
      return [this.jsonschema.toFieldConfig(step) as FormlyFieldConfig];
    });
  }
}
