import { ApplicationService, ApplicationType, IApplication } from '@c8y/client';
import { AppStateService, gettext } from '@c8y/ngx-components';
import { flatMap } from 'lodash-es';
import { ApplicationExtension, ExtensibleDeviceRegistrationProvider } from './base-device-registration.model';

export class BaseExtensibleDeviceRegistrationService {
  constructor(
    protected applicationService: ApplicationService,
    protected stateService: AppStateService
  ) {}

  protected getMetadataUrl(contextPath: string): string {
    return `${this.getServiceBaseUrl(contextPath)}/metadata`;
  }

  protected getServiceBaseUrl(contextPath: string): string {
    if (!contextPath || contextPath.length === 0) {
      throw new Error(gettext('The URL of the microservice is misconfigured. A context path is missing.'));
    }
    return `/service/${contextPath}/deviceRegistration`;
  }

  protected async getDeviceRegistrationProviders(
    bulk: boolean
  ): Promise<ExtensibleDeviceRegistrationProvider[]> {
    let providers: ExtensibleDeviceRegistrationProvider[] = [];
    const microservices = await this.listMicroservicesByUser();
    if (microservices && microservices.length > 0) {
      providers = this.getProvidersFromManifestExtensions(microservices, bulk).concat(
        this.getProvidersFromExtensions(microservices, bulk)
      );
    }
    return providers;
  }

  private async listMicroservicesByUser() {
    const { data } = await this.applicationService.listByUser(
      this.stateService?.currentUser?.value?.userName,
      {
        dropOverwrittenApps: true,
        noPaging: true,
        pageSize: 100,
        withTotalPages: true
      }
    );
    return data?.filter(({ type }) => type === ApplicationType.MICROSERVICE);
  }

  private getProvidersFromManifestExtensions(
    applications: IApplication[],
    bulk: boolean
  ): ExtensibleDeviceRegistrationProvider[] {
    return flatMap(applications, application => {
      return application?.manifest?.extensions?.map(extension => {
        return { ...extension, contextPath: application.contextPath } as ApplicationExtension;
      });
    }).filter(provider => this.appSupportingExtensibleDeviceRegistrationForm(provider, bulk));
  }

  private getProvidersFromExtensions(applications: IApplication[], bulk: boolean): ExtensibleDeviceRegistrationProvider[] {
    return flatMap(applications, application => {
      return application.extensions?.map(extension => {
        return { ...extension, contextPath: application.contextPath } as ExtensibleDeviceRegistrationProvider;
      });
    }).filter(provider => this.appSupportingExtensibleDeviceRegistrationForm(provider, bulk));
  }

  private appSupportingExtensibleDeviceRegistrationForm(extension: ExtensibleDeviceRegistrationProvider, bulk: boolean): boolean {
    if (bulk) {
      return extension?.type === 'extensibleBulkDeviceRegistration';
    } else {
      return extension?.type === 'extensibleDeviceRegistration';
    }
  }
}
