import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { ExtensionFactory, ExtensionPoint, fromTriggerOnce } from '@c8y/ngx-components';
import { Observable, Subject } from 'rxjs';
import { shareReplay, startWith } from 'rxjs/operators';
import { RegisterDeviceItem } from './RegisterDeviceItem';

export type RegisterDeviceExtension = RegisterDeviceItem | RegisterDeviceItem[] | ExtensionFactory<RegisterDeviceItem>;
export const HOOK_DEVICE_REGISTRATION = new InjectionToken<RegisterDeviceExtension>('RegisterDevicesFactory');

@Injectable({
  providedIn: 'root'
})
export class RegisterDeviceExtensionService implements ExtensionPoint<RegisterDeviceExtension> {
  items$: Observable<RegisterDeviceItem[]>;
  factories: RegisterDeviceExtension[] = [];
  refreshTrigger: Subject<any> = new Subject();

  constructor(private injector: Injector, router: Router) {
    this.items$ = fromTriggerOnce(
      router,
      [this.refreshTrigger],
      [() => this.injector.get(HOOK_DEVICE_REGISTRATION, []), () => this.factories, this]
    ).pipe(startWith([]), shareReplay(1));
  }

  refresh() {
    this.refreshTrigger.next();
  }
}
